<template>
  <div>
    <h2>게시판 리스트</h2>
      <table>
      <thead>
        <tr>
            <td>번호</td>
            <td>글 제목</td>
            <td>글쓴이</td>
            <td>작성일</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data_list in data.datas" v-bind:key="data_list">
            <td>{{data_list.idx}}</td>
            <td>{{data_list.board_title}}</td>
            <td>{{data_list.board_writer}}</td>
            <td>{{data_list.board_regdate}}</td>
        </tr>
      </tbody>

  </table>
  </div>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import Buefy from "buefy";
import "buefy/dist/buefy.css";

Vue.use(Buefy);

export default {

  data() {
      return {
        //   data:[]
        data : {
            datas:[]
        }
      }
  },
  created(){
    const baseURL = "http://localhost:8088";
    axios.get(`${baseURL}/board/list`)
      .then((result) => {
          console.log(result)
          this.data.datas = result.data
      })
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
table{
    margin: 0 auto;
}
td{
    width: 20%;
}
</style>