import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '../components/HelloWorld.vue';
import List from '../components/board/List.vue';

Vue.use(Router);

export default new Router({
    mode:'history',
    routes:[
        {
            path:'/'
            , name: HelloWorld
            , component:HelloWorld
        },
        {
            path:'/board/list'
            , name: List
            , component: List
        }
    ]
})