import Vue from 'vue'
import App from './App.vue'
import router from './routes' // routes 폴더에 있는 모든 것을 router 라는 이름으로 import
import axios from 'axios'

// axios 전역변수 설정
// this.@axios 로 호출 가능 
Vue.prototype.$axios = axios;
Vue.config.productionTip = true


new Vue({
  render: h => h(App),
  router // vue 에 설정
}).$mount('#app')
